import { Component } from '@angular/core';
import { PushNotifications } from "@capacitor/push-notifications";
import { RestService } from './service/rest.service';


import { LoadingController, Platform } from '@ionic/angular';
import { AudioService } from './service/audio.service';
import { Router } from '@angular/router';

@Component({
    selector: 'app-root',
    templateUrl: 'app.component.html',
    styleUrls: ['app.component.scss'],
})
export class AppComponent {

    constructor(private restService: RestService, private platform: Platform, private audio: AudioService, private router: Router ,private loading:LoadingController ) {

        this.loading.create({
            message:"Loading ..."
        }).then((loading)=>{
            loading.present();

            setTimeout(() => {
                loading.dismiss();
            }, 3000);
        });
        
        this.audio.playSound();
        this.platform.pause.subscribe(async () => {
            console.log('Change', 'Pause event detected');
            this.audio.stopSound()
        });
        this.platform.resume.subscribe(async () => {
            // alert('Resume event detected');
            this.audio.playSound()
        });

        PushNotifications.addListener('registration',
            (token) => {
                console.log('Token => ', token.value);

                this.restService.postRequestToken('users/set-device-token', { deviceToken: token }).subscribe(res => {
                    console.log('Token --->', res);
                }, error => {
                    console.log('TOken UPdate Error', error);

                })
            }
        );

        // Some issue with our setup and push will not work
        PushNotifications.addListener('registrationError',
            (error: any) => {

                alert('Error on registration: ' + JSON.stringify(error));
            }
        );

    }

}
